<template>
    <div v-if="product">
        <b-card-group deck>
          <b-card class="cardy" style="border-radius: 0px; border-width: 0px;">
            <div class="row">
              <div class="col">
                <h4 v-if="product.post_title">
                  {{product.post_title}}
                </h4>
                <b-card-text class="card-text">
                    <span v-if="product.acf.top_text">
                        {{product.acf.top_text}}
                    </span>
                </b-card-text>
              </div>
              <div class="col-xs-3 mx-auto title-text-alternative text-center">


                <div v-if="product.acf.image">
                  <img :src="product.acf.image.url"/>
                </div>

                <button v-if="product.acf.collapsable"
                @click="showCollapse = !showCollapse"
                type="submit"
                class="flat-butt butt"
                :style="{ 'background-color': this.$store.state.quarternary_colour}"
                id="product-butt">
                  {{product.acf.collapsable}}
                </button> 

                <a v-if="product.acf.Scroll_contactform_button"
                class="flat-butt butt"
                :style="{ 'background-color': this.$store.state.tertiary_colour}"
                v-scroll-to="'#contact-area'"
                id="product-butt">

                  <font-awesome-icon icon="cogs"></font-awesome-icon>
                  <span style="padding-left: 10px;">{{product.acf.Scroll_contactform_button}}</span>
                
                </a>

                <button v-if="downloadPresent && !cardIsCollapsable"
                      class="flat-butt butt"
                      :style="{ 'background-color': this.$store.state.tertiary_colour}"
                      @click="handleDownload(product.acf.download_file)">
                        {{product.acf.download_button}}
                </button> 

              </div>
            </div>
            <div>
              <b-collapse id="collapse-4 collapse-section" class="mt-2"
                 v-if="cardIsCollapsable"
                 v-model="showCollapse" >

                 <div>
                    <div>
                        <span class="content-styles" v-html="product.post_content"></span>
                    </div>

                    <b class="flat-butt text-center butt" v-if="product.acf.collapse_span"
                      :style="{ 'background-color': this.$store.state.tertiary_colour}">
                      {{product.acf.collapse_span}}
                    </b>

                    <b class="flat-butt text-center butt"
                        :style="{ 'background-color': this.$store.state.tertiary_colour}"
                        v-if="downloadPresent"
                        @click="handleDownload(product.acf.download_file)">
                            {{product.acf.download_button}}
                    </b>

                 </div>
              </b-collapse>
              <b-collapse id="collapse-4 collapse-section" class="mt-2"
                v-model="showForm">

                    <div class="row justify-content-center">
                      <p>
                        <b>Please enter your details to download the file</b>
                      </p>
                    </div>
                  
                    <contact-form
                    formType="Download request"
                    :location="this.location"
                    :enquiryPresent="false"
                    buttonText="Request Download"
                    :isDownload="true"
                    ></contact-form>

              </b-collapse>
            </div>
        </b-card>
    </b-card-group>
    </div>
</template>

<script>
import NavigateTo from "@/mixins/navigateto.js"
import ContactForm from "@/components/ContactForm.vue"

export default {
    mixins: [NavigateTo],
    components: {
      ContactForm
    },
    props: {
        product: {
            type: Object
        }
    },
    computed: {
      cardIsCollapsable() {
        return this.product.acf.collapsable != ""
      },
      downloadPresent() {
        return this.product.acf.download_button != ""
              && this.product.acf.download_file
      },
      location() {
        return this.product.acf.download_file != false ? this.product.acf.download_file : ''
      }
    },
    data() {
        return {
            showCollapse: false,
            showForm: false
        };
    },
    methods: {
        collapsableClick() {
            if(this.product.acf.download_file) {
                this.navigateTo(this.product.acf.download_file)
            }
        },
        handleDownload() {
          if(!this.product.acf.download_form) { //need to swich this to be false
             this.navigateTo(this.product.acf.download_file)
          } else {
            this.showForm = !this.showForm
          }
        }
    }
}
</script>

<style scoped>
.content-styles >>> hr {
    height: 2px;
    margin-bottom: 4%;
}
.cardy {
  background-color: #f5f5f5;
}

.card-text {
  padding-bottom: 15px;
}

img {
  width: 100%;
}

#product-butt {
  width: 100%;
  justify-content: center;
}

.butt {
  margin-top: 10px;
}

.text-nowrap {
    white-space: nowrap;
}
</style>