<template>
  <div>
    <div class="container page-content">
        <div class="row" v-if="products[0]">
          <div class="col" id="text-block-one">
            <div>
              <h2 class="title-text">{{products[0].product_name}}</h2>
              <hr class="line" :style="{'background-color': this.$store.state.primary_colour }"/> 
            </div>
            <div v-if="products[0].product_description">
              {{products[0].product_description}}
            </div>
          </div>
        </div>
        <div class="row"
          v-for="(item, index) in products"
          :key="index">
          <div class="container-fluid banner" id="three-bar">
              <div class="container page-content">
                <product :product="item"/>
              </div>
          </div>
        </div>
    </div>
    
  </div>
</template>

<script>
import Product from "@/views/PageContent/Product.vue"

export default {
  name: "ProductBlock",
  components: {
    Product
  },
  props: {
    productName: {
      type: String
    }
  },
  computed: {
    title() {
      return "get the title"
    },
    text() {
      return "some text"
    },
    posts() {
      return this.$store.state.posts
    },
    products() {
      let sortedPosts = this.$store.getters.sortedPosts
      let productList = []
      sortedPosts.forEach(x => {
          x.tags.forEach(y => {
            if(y.slug == this.productName)  {
                x.product_name = y.name
                x.product_description = y.description
                productList.push(x)
              }
        })
      });
      return productList
    }
  }
};
</script>

