<template>
  <div class="container page-content" v-if="this.$store.state.pages">
    <div class="row">
      <page-content :contentProp="resources.post_content" :sidebarProp="resources.acf.sidebar"></page-content>
    </div>
    <div v-if="productList.length > 0">
      <div v-for="(productName, index) in productList"
        :key="index">
          <product-block
          :productName="productName"/>
      </div>
    </div>
  </div>
</template>

<script>
import PageContent from "@/views/PageContent/PageContent.vue";
import ProductBlock from "@/views/PageContent/ProductBlock.vue";

export default {
  components: {
    ProductBlock,
    PageContent
  },
  computed: {
    resources() {
      return this.$store.state.pages.find(x => x.post_title == "Resources")
    },
    productList() {
        return this.resources.acf.products.split(' ')
    }
  }
};
</script>

