<template>
  <div class="container page-content">
    <div class="row">
      <page-content
        :contentProp="softwareSolutions.post_content"
        :sidebarProp="softwareSolutions.acf.sidebar">
      </page-content>
    </div>
    <div v-if="productList.length > 0">
      <div v-for="(productName, index) in productList"
        :key="index">
          <product-block
          :productName="productName"/>
      </div>
    </div>
  </div>
</template>

<script>
import PageContent from "@/views/PageContent/PageContent.vue"
import ProductBlock from "@/views/PageContent/ProductBlock.vue";

export default {
  components: {
    PageContent,
    ProductBlock
  },
  computed: {
    softwareSolutions() {
      return this.$store.state.pages.find(x => x.post_title == 'Software Solutions')
    },
    productList() {
        return this.softwareSolutions.acf.products.split(' ')
    }
  }
};
</script>

